  	gettop(status)
  	{

        if (status=='Y')
        {
			const self = this;
			self.setState({ activeStory: 'spinner'  });
			var datain = {
			    action: "get_top",
			    vkid: this.state.vkid,

			};

			var data = new FormData();
			data.append( "json", JSON.stringify( datain ) )


			fetch('https://zakupi.club/telegram_bot/stolici/ajax_stolici.php',{
				    method: 'POST',
				    body: data
				  })
			  .then(
			    function(response) {
			      if (response.status !== 200) {
			        //console.log('response N: ', response);
			        return;
			      }

			      // Examine the text in the response
			      response.json().then(function(data) {
			        //console.log("toplist_array_aj",data.top.vk);


					//console.log("response Y",data.answers[0].var1);

					if (data.success=="Y")
					{
	        			//self.setState({ answers: data.answers });
	        			self.setState({top_list: data.top.vk});
	        			self.setState({ activeStory: 'top'  });

					} else
					{
	                	self.setState({ activeStory: 'top'  });

						self.setState({ popout:
						      <Alert
						        actions={[{
						          title: 'OK',
						          autoclose: true,
						        }]}
						        onClose={self.closePopout}
						      >
						        <h2>Ошибка!</h2>
						        <h2>{data.errors}</h2>
						      </Alert>
						    });


					}


			      });
			    }
			  );
			  self.setState({updatetop : 'N'})
		  }

  	}

