	onStoryChange (e)
	{

        const self = this;
        if (e.currentTarget.dataset.story==="top")
        {
        	//self.gettop();
        	self.setState({updatetop : 'Y'})
        }
        if (e.currentTarget.dataset.story==="game")
        {
        	//self.gettop();
        	if (self.state.firststart == "Y" ) self.get_question();
        	//self.setState({firststart : 'N'})
        }


		this.setState({ activeStory: e.currentTarget.dataset.story });
		if (e.currentTarget.dataset.story!="top") window.scrollTo(0, 0)
		if (e.currentTarget.dataset.story=="lk ") window.scrollTo(0, 0);
		//alert(e.currentTarget.dataset.story);
		if (this.state.activeStory==="top")
		{
			if (e.currentTarget.dataset.story==="lk")
			{
				window.scrollTo(0, 0);
			}

		}


		if (this.state.activeStory==="oferta")
		{
			if (e.currentTarget.dataset.story==="top")
			{
				window.scrollTo(300, 300);
			}
		}

		if (this.state.activeStory==="pricelist")
		{
			if (e.currentTarget.dataset.story==="top")
			{
				window.scrollTo(0, 0);
			}
		}
	}

	checkInternet = () =>
	  {
		let status = this.state.status;
		status = window.navigator.onLine ? 'online' : 'offline';
		this.setState({ status: status });
		if (status === 'offline')
		{
			this.setState({ activePanel: 'off', activeStory: 'off' })
		}
		else if (status === 'online')
		{
			if (this.state.activePanel === 'off')
			{
				//тут перемещение на главную страницу
				this.setState({ activePanel: 'main', activeStory: 'main' })
			}
		}

	  }


	  menu (e)
		{
			//перехват системной кнопки назад++
			const self = this;
			//console.log("e.state",e.state);
			if (this.state.activeStory==="oferta")
			{
				self.setState({ activePanel: 'order', activeStory: 'order'  });
				window.history.pushState({ panel: 'order'},`order`);
				window.scrollTo(300, 300);
			}
			else if (this.state.activeStory!=="main")
			{


				if (e.state)
				{
					//alert(e.state.panel);
					self.setState({ activePanel: e.state.panel, activeStory: e.state.panel });

				} else
				{
					self.setState({ activePanel: 'main', activeStory: 'main' , search: '' });
					window.history.pushState({ panel: 'main'},`main`);
				}
			}
			else
			{
		    }
		    //перехват системной кнопки назад--
	}
	secondsToTime(secs){
	    let obj = {
	      "h": 0,
	      "m": 0,
	      "s": secs
	    };
	    return obj;
	    /*

	    let hours = Math.floor(secs / (60 * 60));

	    let divisor_for_minutes = secs % (60 * 60);
	    let minutes = Math.floor(divisor_for_minutes / 60);

	    let divisor_for_seconds = divisor_for_minutes % 60;
	    let seconds = Math.ceil(divisor_for_seconds);

	    let obj = {
	      "h": hours,
	      "m": minutes,
	      "s": seconds
	    };
	    return obj;
	    */
	  }

	startTimer(state) {
	    const self = this;

	    if (state == "new" )
	    {
	    	clearInterval(this.timer);
	    	this.timer = 0;
	    }
	    if (this.state.seconds > 0) {
	      this.timer = setInterval(this.countDown, 1000);
	    }

	  }

	countDown() {
	    // Remove one second, set state so a re-render happens.
	    if (this.state.seconds > 0 && this.state.cur_answer === "")
	    {
		    let seconds = this.state.seconds - 1;
		    this.setState({
		      time: this.secondsToTime(seconds),
		      seconds: seconds,
		    });

		    // Check if we're at zero.
		    if (seconds == 0) {
		      clearInterval(this.timer);
		      if (this.state.cur_answer === "")
		      {
			      this.setState({hash_var1 : ''});
			      this.setState({hash_var2 : ''});
			      this.setState({hash_var3 : ''});
			      this.setState({hash_var4 : ''});
			      this.setState({ cur_answer: 'next_q'})
			      this.checkvar("timeout");
			   }

		    }
	    }
	}

	componentDidMount() {
		const self = this;
	    //let timeLeftVar = this.secondsToTime(this.state.seconds);
	    let timeLeftVar = (this.state.seconds);
	    this.setState({ time: timeLeftVar });

		window.setInterval(this.checkInternet, 500);
		//перехват системной кнопки назад++
		window.addEventListener('popstate', e => e.preventDefault() & self.menu(e));
		window.history.pushState( {panel: 'main'}, `main` );
	    //перехват системной кнопки назад--



		connect.subscribe((e) => {
			switch (e.detail.type) {
				//добавление темной темы++
				case 'VKWebAppUpdateConfig':
					//this.setState({ theme: e.detail.data.scheme });
					let schemeAttribute = document.createAttribute('scheme');
					schemeAttribute.value = e.detail.data.scheme ? e.detail.data.scheme : 'client_light';
					document.body.attributes.setNamedItem(schemeAttribute);
					break;
				//добавление темной темы--
				case 'VKWebAppGetUserInfoResult':
					self.setState({ fetchedUser: e.detail.data });
					self.setState({ vkid: e.detail.data.id });
					//self.checkvar("start");
					//Проверка на подписку в группу++
					var datain = {
					    action: "check_user_in_group",
					    vkid: e.detail.data.id,
					};

					var data = new FormData();
					data.append( "json", JSON.stringify( datain ) )


					fetch('https://zakupi.club/telegram_bot/stolici/ajax_stolici.php',{
						    method: 'POST',
						    body: data
						  })
					  .then(
					    function(response) {
					      if (response.status !== 200) {
					        //console.log('response N: ', response);
					        return;
					      }
					      response.json().then(function(data) {
							if (data.success=="Y")
							{
								if (data.in_group=="N")
								{
									//184431774
									connect.send("VKWebAppJoinGroup", {"group_id": data.group_id});
								}
							}
							else
							{
								self.setState({ popout:
								      <Alert
								        actions={[{
								          title: 'OK',
								          autoclose: true,
								        }]}
								        onClose={self.closePopout}
								      >
								        <h2>Ошибка!</h2>
								        <h2>{data.errors}</h2>
								      </Alert>
								    });
							}

					      });
					    }
					  );
					//Проверка на подписку в группу--
					//self.get_question("start");
					break;
				default:
					//console.log(e.detail.type);
			}
		});
		connect.send('VKWebAppGetUserInfo', {});
		//connect.send("VKWebAppAllowMessagesFromGroup", {"group_id": 1, "key": "dBuBKe1kFcdemzB"});
		//184431774

		//connect.send("VKWebAppAllowMessagesFromGroup", {"group_id": 1, "key": "dBuBKe1kFcdemzB"});
	}

